<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-alert
        title=""
        type="info"
        show-icon
        center
        :closable="false"
      >
      </el-alert>
      <!--   -->
      <el-steps align-center :active="activeIndex - 0" finish-status="success">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form
        :model="addForm"
        :rules="addFormrules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <el-tabs v-model="activeIndex" tab-position="left" :before-leave='beforeTabLeave' @tab-click='tabClicked'>
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                expandTrigger="hover"
                v-model="addForm.goods_cat"

                :options="catelist"
                :props="cateProps"
                clearable
                @change="handleChange"
              >
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
               <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
                  <el-checkbox-group v-model="item.attr_vals">
                      <el-checkbox :label="cb" v-for="(cb,i) in item.attr_vals" :key="i"></el-checkbox>

                  </el-checkbox-group>

               </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <!-- item -->
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
                <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
             
           
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!--  -->
            <!-- action api  -->
            <el-upload
                
                action="https://lianghj.top:8888/api/private/v1/upload"
                :headers="headerObj"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="fileList"
                :on-success="handleSuccess"
                list-type="picture">
                <el-button size="small" type="primary"></el-button>
                
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
              <quill-editor
                  
                  v-model="addForm.goods_introduce"
                  
                >
              </quill-editor>

           <el-button @click="addGoods" type="primary" class="btnAdd"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="picdialogVisible"
      width="50%"
     >
      <img :src="previewPath" alt="" class="previewImg">
     
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      activeIndex: "0",
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat:[],
        pics:[],
        //
        goods_introduce:'',
        attrs:[]
      },
      addFormrules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 10, message: "2-10", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_cat: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      
      catelist: [],
      cateProps:{
          value: "cat_id",
          label: "cat_name",
          children: "children",
      },
      manyTableData:[],
      onlyTableData:[],
      fileList:[],
      headerObj:{
        Authorization:window.sessionStorage.getItem('token')
      },
      previewPath:'',
      picdialogVisible:false
    };
  },

  created() {
    this.getCateList();
  },
  methods: {
    async getCateList() {
      const { data: res } = await this.$http.get("categories");
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }

      this.catelist = res.data;
      // 
    },
    //   
    handleChange(){
        if(this.addForm.goods_cat.length!==3){
            this.addForm.goods_cat=[]
        }
    },
    beforeTabLeave(activeName, oldActiveName){
        if(oldActiveName=='0' && this.addForm.goods_cat.length!==3){
            this.$message.error('')
            return false
        }
    },
   async tabClicked(){
        //  
       if(this.activeIndex==='1'){
        const{data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{
               params:{
                   sel:'many'
               }
           })
        if(res.meta.status!==200){
            this.$message.error('')
        }
        res.data.forEach(item => {
            item.attr_vals=item.attr_vals.length === 0 ? []:item.attr_vals.split(' ')
           
        });
        this.manyTableData=res.data
       }else if(this.activeIndex==='2'){
        const{data:res} = await this.$http.get(`categories/${this.cateId}/attributes`,{
            params:{
              sel:'only'
            }
          })
         if(res.meta.status!==200){
            this.$message.error('')
        }
        this.onlyTableData=res.data

       } 
    },
  //   
  handlePreview(file){
    console.log(file);
    this.previewPath ="https://lianghj.top:8888/"+ file.response.data.tmp_path
    this.picdialogVisible=true
  },
  // 
  handleRemove(file){
    //1.
    //2.pics 
    //3.splice  pics 
    const filePath = file.response.data.tmp_path
    const i= this.addForm.pics.findIndex(x=>x.pic=== filePath)
    this.addForm.pics.splice(i,1)
    console.log(this.addForm);
  },
  //  
  handleSuccess(response){
    //
    //  Pushpics  
    const picInfo ={ pic:response.data.tmp_path}
    this.addForm.pics.push(picInfo) 
    console.log(this.addForm);
  },
  addGoods(){
    // addform goods_cat  ,  
    //   addform   
    
      this.$refs.addFormRef.validate(async valid=>{
            if(!valid){
              return this.$message.error('')
            }
          const form = _.cloneDeep(this.addForm)
          form.goods_cat=form.goods_cat.join(',')
      //    
      this.manyTableData.forEach(item=>{
         const newInfo ={
           attr_id:item.attr_id,
           attr_value:item.attr_vals.join(' ')
         }
         this.addForm.attrs.push(newInfo)
      })
      this.onlyTableData.forEach(item=>{
         const newInfo ={
            attr_id:item.attr_id,
           attr_value:item.attr_vals
         }
          this.addForm.attrs.push(newInfo)
      })
      form.attrs = this.addForm.attrs

         const{data:res} =await  this.$http.post('goods',form);
        if(res.meta.status!==201){
         return   this.$message.error('')
        }
        this.$message.success('')   
        this.$router.push('/goods')    
             
       })
  }
  
  },
  computed:{
      cateId(){
          if(this.addForm.goods_cat.length === 3){
              return  this.addForm.goods_cat[2]
          }
          return null
      }
  },
 
  
  };
</script>

<style lang="less" scoped>
.el-card {
  margin-top: 15px;
}
.el-tabs {
  margin-top: 35px;
}
.el-checkbox{
 margin: 0 10px 0 0  !important ; 
}
.previewImg{
  width: 100%;
  height: 100%;
}
.btnAdd{
  margin-top: 10px;
}

</style>