<template>
    <div>
        <!--  -->
        <el-breadcrumb separator="/" >
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
             <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <!--  -->
        <el-row :gutter="10">
            <el-col :span="7"><div class="grid-content bg-purple">   
                 <el-input placeholder="" class="input-with-select" v-model="queryInfo.query" clearable @clear='getUserList'>
                     <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                </el-input>
                </div>
            </el-col>
            <el-col :span="4">
                <div class="grid-content bg-purple">
                  <el-button type="primary" @click="addDialogVisible=true "></el-button>
                </div>
            </el-col>
        </el-row>
       <!--  -->
          <el-table :data="userlist" style="width: 100% ;" :border='true' stripe class="userCard">
             <el-table-column type="index"  label="#"></el-table-column>
            <el-table-column prop="username" label="" width="180"></el-table-column>
            <el-table-column prop="email" label="" width="180"></el-table-column>
            <el-table-column prop="mobile" label="" width="180"></el-table-column>
            <el-table-column prop="role_name" label="" width="180"></el-table-column>
            <el-table-column prop="mg_state" label="" width="180">
               <template slot-scope="scope"> <!--  -->
                   
                    <el-switch
                      v-model="scope.row.mg_state" @change="userStateChanged(scope.row)">
                     
                    </el-switch>
               </template>
            </el-table-column>
            <el-table-column  label="" >
                <template slot-scope="scope">
                   
                    <!--  -->
                    <el-button type="primary" icon="el-icon-edit" size="small" @click="showEditDialog(scope.row.id)"></el-button>
                    <!--   -->
                    <el-button type="danger" icon="el-icon-delete"  size="small" @click="removeUserById(scope.row.id)"></el-button>
                    <!--  -->
                    <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                        <el-button type="warning" icon="el-icon-setting"  size="small" @click="setRole(scope.row)"></el-button>
                    </el-tooltip>
                   
                </template>
            </el-table-column>
          
      </el-table>
      <!--  -->
       <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[1,2,3,5,10]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
        </el-pagination>



        </el-card>

       <el-dialog
          title=""
          :visible.sync="addDialogVisible"
          width="50%"  @close='addDialogClosed'
         >
         <!--  -->
          
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
           <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
           <el-form-item label="" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
           <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
            <span>
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addUser"> </el-button>
          </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editdialogVisible"
        width="50%" @close="editDialogClosed"
       >
          <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
          <el-form-item label="" >
            <el-input v-model="editForm.username" disabled></el-input>
          </el-form-item>
           <el-form-item label="" prop="mobile">
            <el-input v-model="editForm.mobile"></el-input>
          </el-form-item>
           <el-form-item label="" prop="email">
            <el-input v-model="editForm.email"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editdialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="setRoleDialogVisible"
        width="50%"
      @close='setRoleDialogClosed' >
        <div>
          <p>:{{userInfo.username}}</p>
          <p>:{{userInfo.role_name}}</p>
          <p>: 
             <el-select v-model="selectedRoleId" placeholder="">
                <el-option
                  v-for="item in roleList"
                  :key="item.id"
                  :label="item.roleName"
                  :value="item.id">
                </el-option>
            </el-select>
          </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRoleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="saveRoleInfo"> </el-button>
        </span>
</el-dialog>
    </div> 
</template>


<script>
export default {
    data() {
      // 
      var checkEmail =(rule,value,cb)=>{
        const regEmail =/^([a-zA-Z0-P_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if(regEmail.test(value)){
          return cb()
        }
        cb(new Error(''))
     }
     //  
      var checkMobile =(rule,value,cb)=>{
        const regEmail =/^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        if(regEmail.test(value)){
          return cb()
        }
        cb(new Error(''))
     }
      return {
        //  
        queryInfo:{
          query:'',
          // 
          pagenum:1,
          //  
          pagesize:2
        },
        userlist:[],
        total:0,
        //  
        addDialogVisible:false,
        editdialogVisible:false,
        addForm:{
          username:'',
          password:'',
          email:'',
          mobile:''
        },
        //  
        addFormRules:{
          username:[
            { required: true, message: '', trigger: 'blur' },
            {min:3,max:10,message:'3-10',trigger: 'blur'}
          ],
          password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: '6', trigger: 'blur' }
          ],
           email: [
          { required: true, message: '', trigger: 'blur',},
          { validator: checkEmail, trigger: 'blur'}
         
          ],
           mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur'}
          ],

         
        },
        editForm:{
          username:'',
          email:'',
          mobile:''
        },
         editFormRules:{
          
          
           email: [
          { required: true, message: '', trigger: 'blur',},
          { validator: checkEmail, trigger: 'blur'}
         
          ],
           mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur'}
          ],

         
        },
        setRoleDialogVisible:false,
        //  
        userInfo:{},
        //  
        roleList:[],
        //  
        selectedRoleId:''
      }
    },

    created(){
      this.getUserList()
    },
    methods:{
   async   getUserList(){
       const {data:res} =  await this.$http.get('users',{params:this.queryInfo})
        if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
        console.log(res);
        this.userlist =res.data.users
        this.total=res.data.total
      },
      //  pagesize  
      handleSizeChange(newSize){
          this.queryInfo.pagesize = newSize
          this.getUserList();
      },
      handleCurrentChange(newPage){
          this.queryInfo.pagenum=newPage
          this.getUserList();
      },
      async userStateChanged(userinfo){
        console.log(userinfo);
       const {data:res} = await this.$http.put(`users/${userinfo.id}/state/:${userinfo.mg_state}`)
   
      },
      //  
      addDialogClosed(){
        this.$refs.addFormRef.resetFields()
      },
      addUser(){
        this.$refs.addFormRef.validate(async valid=>{
           if (!valid) return
           const { data: res } = await this.$http.post('users', this.addForm)
            if (res.meta.status !== 201) return this.$message.error('');
            this.$message.success('');
              this.addDialogVisible=false
              this.getUserList();
           
       })
      },
    async  showEditDialog(id){
         const{data:res} = await this.$http.get('users/'+id);
         if (res.meta.status !== 200 ){
           return this.$message.error('')
         }
    
         this.editForm= res.data
        this.editdialogVisible=true;
      },
      editDialogClosed(){
        this.$refs.editFormRef.resetFields();
      },
      //  
      editUserInfo(){
        this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return
         const{data:res}= await this.$http.put('users/'+ this.editForm.id,{
              email:this.editForm.email,
              mobile:this.editForm.mobile
            }) 
          if(res.meta.status !== 200 ){
           return  this.$message.error('')
          }
          this.$message.success('')
          this.editdialogVisible=false;
          this.getUserList();
        })
      },
      // ID 
    async  removeUserById(id){
      const confirmResult = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
        ).catch(err=>err)
        if( confirmResult !=='confirm'){
          return this.$message.info('')
        }
        const {data:res}= await this.$http.delete('users/'+id);
        if(res.meta.status!==200){
            this.$message.error('')
        }
         this.$message.success('')
          this.getUserList(); 
      },
    async  setRole(userInfo){
        this.userInfo=userInfo;
       const{data:res}= await this.$http.get('roles');
       this.roleList=res.data
        this.setRoleDialogVisible=true
      },
   async   saveRoleInfo(){
        if(!this.selectedRoleId){
          return this.$message.error('')
        }
      const{data:res} = await this.$http.put(`users/${this.userInfo.id}/role`,{
          rid:this.selectedRoleId
        })
      if(res.meta.status!==200){
        return   this.$message.error('')
      }
      
      this.$message.success('')
      this.getUserList();
      this.setRoleDialogVisible=false;
      },
      setRoleDialogClosed(){
        this.selectedRoleId='',
        this.userInfo={}
      }
      

    }


}
</script>


<style lang="less">
  .text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 100%;
    margin-top: 20px;
    box-shadow: 0 1px 1px rgba(0, 0, 0,0.15) !important;
  }
  .userCard{
    margin-top: 15px;
  }
 
</style>