<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-alert title="" type="warning" show-icon> </el-alert>
      <el-row class="cat_opt">
          <el-col>
              <span> :</span> &nbsp;&nbsp;
              <!--  -->
                <el-cascader
                    :show-all-levels="false"
                    v-model="selectKeys"
                    :options="cateList"
                    :props="cateProps"
                    @change="handleChange">
                </el-cascader>
          </el-col>
      </el-row>
      <!-- tab -->
       <el-tabs v-model="activeName" @tab-click="handleTabClick">
    <!--  -->
    <el-tab-pane label="" name="many">
      <el-button type="primary" size="mini" :disabled='isBtnDisabled' @click="showAddDialog" ></el-button>
    <!--  -->
     <el-table
      :data="manyTableData"
       border stripe>
        
        <el-table-column type="expand"> 
            <template slot-scope="scope">
                <el-tag v-for="(item ,i) in scope.row.attr_vals " :key="i"  closable
  :disable-transitions="false"
  @close="handleClose(i,scope.row)">{{item}}</el-tag>
          <!-- new tag  -->
              <el-input
                 
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
             </el-input>
    <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

            </template>


        </el-table-column>
      <el-table-column
        type="index"
        label="#"
        >
      </el-table-column>
      <el-table-column
        prop="attr_name"
        label=""
       >
      </el-table-column>
      <el-table-column label="">
          <template slot-scope="scope">
               <el-button type="primary" size="mini" icon="el-icon-edit"
      @click="showEditDialog(scope.row)"      ></el-button
          >
          <el-button
            type="danger"
            size="mini"
            icon="el-icon-delete"
            @click="removeParams(scope.row)" 
            ></el-button
          >
          </template>
      </el-table-column>
     
    </el-table>
    </el-tab-pane>
    <!--   -->
    <el-tab-pane label="" name="only">
      <el-button type="primary" size="mini" :disabled='isBtnDisabled'  @click="showAddDialog" ></el-button>
    <!--  -->
    <el-table
      :data="onlyTableData"
       border stripe>
       <!--  -->
       <el-table-column type="expand"> 
            <template slot-scope="scope">
                <el-tag v-for="(item ,i) in scope.row.attr_vals " :key="i"  closable
  :disable-transitions="false"
  @close="handleClose(i,scope.row)">{{item}}</el-tag>
          <!-- new tag  -->
              <el-input
                 
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
             </el-input>
    <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

            </template>


        </el-table-column>
      <el-table-column
        type="index"
        label="#"
        >
      </el-table-column>
      <el-table-column
        prop="attr_name"
        label=""
       >
      </el-table-column>
      <el-table-column label="">
          <template slot-scope="scope">
               <el-button type="primary" size="mini" icon="el-icon-edit"
     @click="showEditDialog(scope.row)"  ></el-button
          >
          <el-button
            type="danger"
            size="mini"
            icon="el-icon-delete"
           @click="removeParams(scope.row)"
            ></el-button
          >
          </template>
      </el-table-column>
     
    </el-table>
    </el-tab-pane>
   
     
  </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog
  :title="''+titleText"
  :visible.sync="addParamsDialogVisible"
  width="50%" @close='addParamsDialogClosed'
  >
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" >
  <el-form-item :label="titleText" prop="attr_name">
    <el-input v-model="addForm.attr_name"></el-input>
  </el-form-item>
   </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="addParamsDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addParams" > </el-button>
  </span>
</el-dialog>

<!--  -->
 <el-dialog
  :title="''+titleText"
  :visible.sync="editParamsDialogVisible"
  width="50%" @close='editParamsDialogClosed'
  >
    <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" >
  <el-form-item :label="titleText" prop="attr_name">
    <el-input v-model="editForm.attr_name"></el-input>
  </el-form-item>
   </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editParamsDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editParams" > </el-button>
  </span>
</el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cateList:[],
      cateProps: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      // 
      selectKeys:[],
      activeName:'many',
      manyTableData:[],
      onlyTableData:[],
      addParamsDialogVisible:false,
      // 
      addForm:{
        attr_name:''
      },
      //  
      addFormRules:{
         attr_name:[
            { required: true, message: '', trigger: 'blur' },
            {min:3,max:10,message:'3-10',trigger: 'blur'}
          ],
      },
      editFormRules:{
         attr_name:[
            { required: true, message: '', trigger: 'blur' },
            {min:3,max:10,message:'3-10',trigger: 'blur'}
          ],
      },
      editParamsDialogVisible:false,
       // 
      editForm:{},
     
    };
  },
  created() {
    this.getCateList()
  },
  methods: {
   async getCateList(){
    const{data:res} = await this.$http.get('categories')
    if(res.meta.status!== 200){
      this.$message.error('')
    }
  this.cateList=res.data
    },
    // ,
    handleChange(){
      this.getParamsData();
    },
    // tab 
    handleTabClick(){
      this.getParamsData();
    },
  async  getParamsData(){
    // 
      if(this.selectKeys.length !==3){
        this.selectKeys=[]
        this.manyTableData=[]
         this.onlyTableData=[]
        return 
      }
      // id 
     const{data:res} = await  this.$http.get(`categories/${this.cateId}/attributes`,{params:{sel:this.activeName}})
     if(res.meta.status !==200 ){
      return  this.$message.error('')
     }

     console.log(res.data);
     res.data.forEach(item => {
          item.attr_vals=item.attr_vals ? item.attr_vals.split(' ') : []
          //   
          item.inputValue=''
          item.inputVisible=false
     });

     if(this.activeName === 'many'){
       this.manyTableData=res.data
     }else{
       this.onlyTableData =res.data
     }
    },
    // 
    showAddDialog(){
      this.addParamsDialogVisible=true
    },
    // 
    addParamsDialogClosed(){
      this.$refs.addFormRef.resetFields() ;  
    },
    addParams(){
       this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
         const{data:res} =await  this.$http.post(`categories/${this.cateId}/attributes`,{
              attr_name:this.addForm.attr_name,
              attr_sel:this.activeName
            });
        if(res.meta.status!==201){
         return   this.$message.error('')
        }
          this.$message.success('')   
            this.addParamsDialogVisible=false ;
             this.getParamsData(); 
       })
      
    },
  async  showEditDialog(params){
   const{data:res} =  await this.$http.get(`categories/${this.cateId}/attributes/${params.attr_id}`,{
      params:{ attr_sel:this.activeName}
   })
   this.editForm = res.data
   this.editParamsDialogVisible=true

    },
     editParamsDialogClosed(){
      this.$refs.editFormRef.resetFields() ;  
    },
    editParams(){
       this.$refs.editFormRef.validate(async valid=>{
            if(!valid) return
         const{data:res} =await  this.$http.put(`categories/${this.cateId}/attributes/${this.editForm.attr_id}`,{
              attr_name:this.editForm.attr_name,
              attr_sel:this.activeName
            });
        if(res.meta.status!==200){
         return   this.$message.error('')
        }
          this.$message.success('')   
            this.editParamsDialogVisible=false ;
             this.getParamsData(); 
       })
      
    },
    //
   async removeParams(params){
       const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete(
       `categories/${this.cateId}/attributes/${params.attr_id}`
      );
      if (res.meta.status !== 200) {
        this.$message.error("");
      }
      this.$message.success("");
     this.getParamsData(); 
    },
    //    enter   
 async   handleInputConfirm(row){
      if(row.inputValue.trim().length === 0){
        row.inputValue ='' 
        row.inputVisible=false
        return 
      }
      //  
      row.attr_vals.push(row.inputValue.trim());
      row.inputValue='';
      row.inputVisible=false
      //  
      this.saveAttrVals(row)

    },
    //    
    async saveAttrVals(row){
       const{data:res}= await  this.$http.put(`categories/${this.cateId}/attributes/${row.attr_id}`,{
        attr_name:row.attr_name,
        attr_sel:this.activeName,
        attr_vals:row.attr_vals.join(' ')
      })
      if(res.meta.status!==200){
       return this.$message.error('')
      }
      this.$message.success('')
    },
    //  new tags  
    showInput(row){
        row.inputVisible=true;
        // input   
        // $nextTick 
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
    },
    //  tag
    handleClose(i,row){
        row.attr_vals.splice(i,1)
        this.saveAttrVals(row)
    }
  },
  computed:{
    isBtnDisabled(){
      if(this.selectKeys.length!==3){
        return true
      }else{
        return false
      }
    },
    // id
    cateId(){
      if(this.selectKeys.length===3){
        return this.selectKeys[2]
      }
      return null 
    },
    titleText(){
      if(this.activeName==='many'){
        return ''
      }else{
        return ''
      }
    }
    }
};
</script>

<style lang="less"  scoped>
    .el-card{
        margin-top: 10px;
    }
    .cat_opt{
        margin:15px 0px
    }
    .el-tag{
      margin: 0 6px;
    }
    .el-input{
      width: 120px;
    }
    

</style>
