<template>
    <div>
        <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
      <el-card class="box-card">
          <el-row>
              <el-col>
                  <el-button type="primary"  @click="addDialogVisible=true "></el-button>
              </el-col>
          </el-row> 
          <!--  -->
          <el-table :data="rolelist" style="width: 100% ; margin-top:10px" border stripe>
              <!--  -->
            <el-table-column type="expand">
                <template slot-scope="scope">
                   <el-row :class="['bdbottom',i1===0 ? 'bdtop' : '','vcenter']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                       <!--  -->
                       <el-col :span="5">
                           <el-tag closable  @close="removeRightById(scope.row,item1.id)"> {{item1.authName}}</el-tag>
                           <i class="el-icon-caret-right"></i>
                       </el-col>
                        <!--  -->
                       <el-col :span="19">
                           <el-row :class="['bdbottom',i2===0 ? 'bdtop' : '','vcenter']"  v-for="(item2,i2) in item1.children" :key='item2.id'>
                               <!--  -->
                               <el-col :span="6">
                                   <el-tag type="success" closable  @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                                    <i class="el-icon-caret-right"></i>
                               </el-col>
                               <!--  -->
                               <el-col :span="18">
                                    <el-tag :class="['bdbottom',i3===0 ? 'bdtop' : '']"  type="danger" v-for="(item3,i3) in item2.children" :key="item3.id" closable  @close="removeRightById(scope.row,item3.id)" >{{item3.authName}}</el-tag>
                               </el-col>
                           </el-row>
                       </el-col>
                   </el-row>
                </template>
            </el-table-column>
            <el-table-column type="index" label="#"> </el-table-column>
            <el-table-column prop="roleName" label="" >
            </el-table-column>
            <el-table-column prop="roleDesc" label=""> </el-table-column>
             <el-table-column label="">
                 <template slot-scope="scope">
                     <el-button type="primary" size="mini" icon="el-icon-edit" ></el-button>
                     <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeRole(scope.row)"></el-button>
                     <el-button type="warning" size="mini" icon="el-icon-star-off" @click="showSetRightDialog(scope.row)" @close='setRightDialogClosed'></el-button>
                 </template>
                 
             </el-table-column>
      </el-table>
      </el-card>
    <!--  -->
     <el-dialog
          title=""
          :visible.sync="setRightDialogVisable"
          width="50%"  
         >
         <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
        <span slot="footer" class="dialog-footer">
            <el-button @click="setRightDialogVisable = false"> </el-button>
            <el-button type="primary" @click="allotRights"> </el-button>
        </span>
     </el-dialog>

     <!--  -->
     <el-dialog
          title=""
          :visible.sync="addDialogVisible"
          width="50%" @close='addDialogClosed'
         >
         <!--  -->
          
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="roleName">
            <el-input v-model="addForm.roleName"></el-input>
          </el-form-item>
           <el-form-item label="" prop="roleDesc">
            <el-input v-model="addForm.roleDesc"></el-input>
           </el-form-item>
        </el-form>
            <span>
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addRole"> </el-button>
          </span>
      </el-dialog>
    </div>
</template>


<script>
export default {
    data() {
        return {
            //
            addDialogVisible:false,
            rolelist:[],
            setRightDialogVisable:false,
            rightsList:[],
            /*  */
            treeProps:{
                children:'children',
                label: 'authName',
            },
            //
            defKeys:[],
            roleId:'',
           // 
           addForm:{
                roleName:'',
                roleDesc:''
            },
          //  
          addFormRules:{
          roleName:[
            { required: true, message: '', trigger: 'blur' },
            {min:2,max:10,message:'2-10',trigger: 'blur'}
          ],
          roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 20, message: '5', trigger: 'blur' }
          ],
        },  
        }
    },
    created(){
        this.getRoleList()
    },
    methods:{
        // 
        addRole(){
             this.$refs.addFormRef.validate(async valid=>{
           if (!valid) return
           const { data: res } = await this.$http.post('roles', this.addForm)
            if (res.meta.status !== 201) return this.$message.error('');
            this.$message.success('');
              this.addDialogVisible=false;
              this.getRoleList()
           
       })
        },
        //    
        addDialogClosed(){
           this.$refs.addFormRef.resetFields()
        },
       async getRoleList(){
          const {data:res}= await this.$http.get('roles');
         
          if(res.meta.status!==200){
           return  this.$message.error('')
          }
          this.rolelist=res.data
        },
     async   removeRightById(role,rightId){
       const confirmResult=  await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err=>err)
        if( confirmResult !== 'confirm'){
            return this.$message.info('')
        }
       const{data:res}= await this.$http.delete('roles/'+role.id+'/rights/'+rightId)
       if(res.meta.status!==200){
         return this.$message.error('');
       }
       this.$message.success('')
       role.children=res.data 
        },
      /*  */
      async showSetRightDialog(role){
          this.roleId=role.id;
         const {data:res}= await this.$http.get('rights/tree')
         if(res.meta.status!==200){
             return  this.$message.error('')
         }
        this.rightsList=res.data
        this.getLeafKeys(role,this.defKeys)
         this.setRightDialogVisable=true;
        },
        // id push defKeys 
        getLeafKeys(node,arr){
            if(!node.children){
                return arr.push(node.id)
            }
        
        node.children.forEach(item => {
            this.getLeafKeys(item,arr)
        });
        },
        // 
        setRightDialogClosed(){
            this.defKeys=[]
        },
        //
    async    allotRights(){
            const Keys=[
              ...this.$refs.treeRef.getCheckedKeys(),
              ...this.$refs.treeRef.getHalfCheckedKeys()
            ]
            const idStr =Keys.join(',');
            //
       const{data:res} = await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr}) ;
        if(res.meta.status !==200 ){
            return this.$message.error('')
        }
        this.$message.success('');
        this.getRoleList();
        this.setRightDialogVisable= false
        },
      async  removeRole(roleId){
           const confirmResult = await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
        ).catch(err=>err)
        if( confirmResult !=='confirm'){
          return this.$message.info('')
        }
        const {data:res}= await this.$http.delete('roles/'+roleId);
        if(res.meta.status!==200){
            this.$message.error('')
        }
         this.$message.success('')
           this.getRoleList();
        }
    }
}
</script>

<style lang="less" scoped>
    .el-tag{
        margin: 7px;
    }
    .bdtop{
        border-top: 1px solid #eee;
    }
     .bdbottom{
        border-bottom: 1px solid #eee;
    }
    .vcenter{
        display: flex;
        align-items: center;
    }
</style> 