<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <el-table :data="rightsList" style="width: 100%" stripe>
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="authName" label="" width="180">
        </el-table-column>
        <el-table-column prop="path" label=""> </el-table-column>
        <el-table-column prop="level" label="">
            <template slot-scope="scope">
                <el-tag v-if="scope.row.level==='0' "></el-tag>
                  <el-tag type="success" v-else-if="scope.row.level==='1' "></el-tag>
                    <el-tag type="warning" v-else></el-tag>
            </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rightsList: [],
    };
  },
  /*  */
  created() {
    this.getRightsList();
  },
  methods: {
    async getRightsList() {
      const { data: res } = await this.$http.get("rights/list");
      if (res.meta.status !== 200) {
        this.$message.error("");
      }
      this.rightsList = res.data;
    },
  },
};
</script>

